	module.exports = {"data":{"talkJson":{"description":"Functional Reactive Programming (FRP) is becoming an increasingly common way to structure event- driven code, but the term \"FRP\" actually refers to a large body of increasingly diverse work. This talk will quickly cover the basics of FRP, and then go into a couple different formulations of FRP that people are beginning to use. We will explore how these formulations fit together historically and theoretically.\nFinally, we will see how the particular variation of FRP used in Elm makes it easy to implement hot- swapping and Elm's Time Traveling Debugger.\n\nby Evan Czaplicki (@czaplic) - Prezi \n\nI am a functional programmer at heart, and my broad goal is to provide the tools and learning resources to make functional languages common in industry. I develop Elm 's compiler, tools, and community to make it great for projects within Prezi and in industry in general.","meta":{"duration":"PT40M55S"},"presenter":{"name":null},"slug":"controlling-time-and-space-understanding-the-many-formulations-of-frp-by-evan-czaplicki","source":{"id":"Agu6jipKfYw","provider":"youtube"},"tag":["Functional Reactive Programming","Elm","User Interface (Speech Topic)"],"title":"\"Controlling Time and Space: understanding the many formulations of FRP\" by Evan Czaplicki"},"allTalkJson":{"edges":[{"node":{"description":"Check out our upcoming conferences at http://gotocon.com\nSubscribe to the GOTO Conferences YouTube Channel at https://www.youtube.com/user/GOTOConf...\n\nJanne Jul Jensen - Interaction Designer and Usability Specialist\n\nABSTRACT\nMost developers today are aware of the importance of creating a good user interface with a high level of usability, but many are lacking the methods and techniques that can help in this process. This session will present to the listeners a range of concrete methods and techniques applicable in different phases of a design process, to handle specific challenges. This will include design patterns, personas, wire framing, paper prototype testing, progressive disclosure, card sorting and creative workshops and many of the methods and techniques will be accompanied by examples.\n\nhttps://twitter.com/gotocon\nhttps://www.facebook.com/GOTOConference\nhttp://gotocon.com","meta":{"duration":"PT51M20S"},"presenter":{"name":null},"source":{"id":"7OSkB4BCx00","provider":"youtube"},"slug":"user-interface-ux-techniques-janne-jul-jensen","tag":["Software Development","Janne Jul Jensen","User Interface Design (Field Of Study)","User Interface (Ontology Instance)","User Interface","GOTO Conference","GOTO Conference","GOTO Conference","User Experience Design (Industry)","Technology (Industry)","User Interface (Speech Topic)","UX","Trifork","Design (Industry)","Programming Language","Videos for Developers"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/7OSkB4BCx00/default.jpg","height":90,"width":120}},"title":"User Interface (UX) Techniques • Janne Jul Jensen"}},{"node":{"description":"React is a new a conceptual model for rendering user interfaces in a functional style. In this talk I'll look at how both the user-facing and implementation specifics resemble RESTful architectures and some interesting patterns and properties that fall out of this.\n\nby Pete Hunt (@floydophone) \n\nPete works on Facebook's React team and manages the Instagram.com web team.","meta":{"duration":"PT36M15S"},"presenter":{"name":null},"source":{"id":"IVvHPPcl2TM","provider":"youtube"},"slug":"react-restful-ui-rendering-by-pete-hunt","tag":["Software (Industry)","React","REST","Representational State Transfer (Internet Protocol)","User Interface (Speech Topic)"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/IVvHPPcl2TM/default.jpg","height":90,"width":120}},"title":"\"React: RESTful UI Rendering\" by Pete Hunt"}},{"node":{"description":"Curry On Prague, July 7th 2015\nhttp://curry-on.org\nhttp://2015.ecoop.org","meta":{"duration":"PT58M24S"},"presenter":{"name":null},"source":{"id":"oYk8CKH7OhE","provider":"youtube"},"slug":"evan-czaplicki-lets-be-mainstream-user-focused-design-in-elm-curry-on","tag":["Programming Language","Programmer (Profession)","Conference","Curry On","Prague (City/Town/Village)","Elm Programming Language","Front-End","Functional Reactive Programming","Language design"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/oYk8CKH7OhE/default.jpg","height":90,"width":120}},"title":"Evan Czaplicki - Let's be mainstream! User focused design in Elm - Curry On"}}]}},"pathContext":{"slug":"controlling-time-and-space-understanding-the-many-formulations-of-frp-by-evan-czaplicki","tags":["Functional Reactive Programming","Elm","User Interface (Speech Topic)"]}}

